.bg-img {
	background-color: #020202;
	margin-top: 100px;
	height: 700px;
}
.rocket {
	/* transform: rotate(0deg); */
	position: absolute;
	top: 18%;
	right: 15%;
	z-index: -0.9;
	width: 250px;
}
.orange-circle {
	height: 4rem;
	width: 4rem;
	background: rgb(244, 244, 242);
	background: linear-gradient(
		90deg,
		rgba(244, 244, 242, 1) 0%,
		rgba(233, 131, 14, 1) 0%,
		rgba(226, 100, 50, 1) 42%,
		rgba(242, 0, 121, 1) 75%,
		rgba(53, 200, 200, 1) 100%
	);
	/* margin-top: 5px; */
	filter: blur(10px);

	right: 18%;

	z-index: -1%;
	border-radius: 999px;
	position: absolute;
}
.orange-grid {
	height: 4rem;
	width: 4rem;
	background: rgb(244, 244, 242);
	background: linear-gradient(
		90deg,
		rgba(244, 244, 242, 1) 0%,
		rgba(233, 131, 14, 1) 0%,
		rgba(226, 100, 50, 1) 42%,
		rgba(242, 0, 121, 1) 75%,
		rgba(53, 200, 200, 1) 100%
	);
	margin-bottom: 400px;

	filter: blur(10px);

	left: 18%;

	z-index: -1%;
	border-radius: 999px;
	position: absolute;
}

@media screen and (max-width: 800px) {
	.rocket {
	}
}
