.white-gradient {
	position: absolute;
	width: 20rem;
	height: 20rem;
	background: rgba(128, 26, 154, 0.522);

	filter: blur(110px);
	border-radius: 100px;
}
.bottom-credit {
	width: 100%;
	height: auto;

	padding: 4%;
	font-family: "Audiowide", cursive;
	font-size: large;
	background-color: #454545;
	text-align: center;
}
.bottom-credit h2 {
	color: white;
}
